import React, { Component } from "react";
import ReactDOM from "react-dom";

import "./styles.css";

var pages = {
  // start: {
  //   content: "Start here",
  // label1: "start",
  // },

  start: {
    content: "A",
    label1: "Green",
    label2: "Blue",
    label3: "Red",
    label4: "Brown",
    next: "question2"
  },

  question2: {
    content: "Red",
    label1: "Red",
    label2: "Blue",
    label3: "Green",
    label4: "Brown",
    next: "question3"
  },

  question3: {
    content: "Thursday",
    label1: "Red",
    label2: "Blue",
    label3: "Green",
    label4: "Brown",
    next: "question4"
  },

  question4: {
    content: "Math",
    label1: "Blue",
    label2: "Green",
    label3: "Red",
    label4: "Brown",
    next: "question5"
  },

  question5: {
    content: "DONE"
  }
};

class Page extends Component {
  render() {
    var pageData = pages[this.props.pageName];
    console.log("page data", pageData);
    var goToPage = this.props.goToPage;

    // function goToPage1() {
    //   goToPage(pageData.page1);
    //   console.log("question1");
    // }
    pageData.startTimer = Date.now();
    function clickedButton1() {
      // record click on buton 1
      pageData.endTimer = Date.now();
      pageData.clickedButton = 1;
      console.log("user clicked button", pageData.clickedButton);
      console.log("took", pageData.endTimer - pageData.startTimer, "ms");
      goToPage(pageData.next);
    }

    function clickedButton2() {
      // record click on buton 2
      goToPage(pageData.next);
    }

    var finalContent = "";

    var button1 = "";
    var button2 = "";
    var button3 = "";
    var button4 = "";
    if (this.props.pageName !== "question5") {
      button1 = <button onClick={clickedButton1}>{pageData.label1}</button>;
      button2 = <button onClick={clickedButton2}>{pageData.label2}</button>;
    } else {
      finalContent = JSON.stringify(pages);
    }

    return (
      <div>
        <p>{pageData.content}</p>
        {/* {image} */}
        {button1}
        {button2}
        {button3}
        {button4}
        <p>{finalContent}</p>
      </div>
    );
  }
}

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      page: "start"
    };

    this.goToPage = this.goToPage.bind(this);
  }

  goToPage(pageName) {
    this.setState({
      page: pageName
    });
  }

  render() {
    return (
      <div className="App">
        <Page pageName={this.state.page} goToPage={(p) => this.goToPage(p)} />
      </div>
    );
  }
}

const rootElement = document.getElementById("root");
ReactDOM.render(<App />, rootElement);
